<template>
  <transition name="slide">
    <div class="add-song" v-show="showFlag" @click.stop>
      <div class="header">
        <h1 class="title">添加歌曲到列表</h1>
        <div class="close" @click="hide">
          <i class="icon-close"></i>
        </div>
      </div>
      <div class="search-box-wrapper">
        <search-box ref="box" placeholder="搜索歌曲" @query="onQuery">
        </search-box>
      </div>
      <div class="shortcut" v-show="!query" >
        <switches :switches="switches" 
        :currentIndex="currentIndex"
        @selectItem="switchesItem" />
        <div class="list-wrapper">
          <scroll ref="songList" class="list-scroll" v-if="currentIndex === 0" 
          :data="playHistory" :pull-up-load="true">
            <div class="list-inner">
              <song-list :songs="playHistory" @selectItem="selectSong"></song-list>
            </div>
          </scroll>
          <scroll ref="searchList" :refreshDelay="refreshDelay" 
          class="list-scroll" v-if="currentIndex === 1" 
          :data="searchHistory" :pull-up-load="true">
            <div class="list-inner">
              <search-list @deleted="deleteSearchHistory"
              @selectItem="selectItem" :searches="searchHistory" />
            </div>
          </scroll>
        </div>
      </div>
      <div class="search-result" v-show="query">
        <suggest :query="query" 
        @select=selectSuggest 
        @listScroll="blurInput" >
        </suggest>
      </div>
      <top-tip ref="showTip">
        <div class="tip-title">
          <i class="icon-ok"></i>
          <span class="text">1首歌曲已添加播放列表</span>
        </div>
      </top-tip>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
import SearchBox from '../searchbox/SearchBox.vue'
import SearchList from 'content/searchlist/SearchList.vue'
import Suggest from 'content/suggest/Suggest.vue'
import Switches from 'content/switches/Switches.vue'
import Scroll from 'common/scroll/Scroll.vue'
import SongList from 'content/songlist/SongList.vue'
import TopTip from 'common/toptip/TopTip.vue'
import{searchMixin}from 'common/mixin.js'
import {Song} from 'network/singerList.js'
import {mapActions, mapGetters} from 'vuex'
  export default {
    mixins: [searchMixin],
    data() {
      return {
      showFlag:false,
      currentIndex:0,
      switches:[
        {name:'最近播放'},
        {name:'搜索历史'}
      ]
      }
    },
    computed: {
      ...mapGetters(['playHistory','searchHistory'])
    },
    components: {
        SearchBox,
        SearchList,
        Suggest,
        Switches,
        Scroll,
        SongList,
        TopTip

    },
    methods: {
      show(){
        this.showFlag = true
        if(this.currentIndex === 0){
          this.$refs.songList.refresh()
        }else{
          this.$refs.searchList.refresh()
        }
      },
      hide(){
        this.showFlag = false
      },
      selectSuggest(){
        this.saveSearch()
        this.showTip()
      },
      switchesItem(index){
          this.currentIndex = index
      },
      selectSong(song,index){
        if(index !== 0){
          this.insertSong(new Song(song))//从storage里面拿到的song具有属性但不是实例，所有要转换一下
          this.showTip()
        }
      },
      showTip(){
        this.$refs.showTip.show()
      },
      ...mapActions(['insertSong','deleteSearchHistory'])
    },
  }
</script>

<style scoped lang="stylus">
  @import "~assets/stylus/variable"
  @import "~assets/stylus/mixin"

  .add-song
    position: fixed
    top: 0
    bottom: 0
    width: 100%
    z-index: 200
    background: $color-background
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform: translate3d(100%, 0, 0)
    .header
      position: relative
      height: 44px
      text-align: center
      .title
        line-height: 44px
        font-size: $font-size-large
        color: $color-text
      .close
        position: absolute
        top: 0
        right: 8px
        .icon-close
          display: block
          padding: 12px
          font-size: 20px
          color: $color-theme

    .search-box-wrapper
      margin: 20px
    .shortcut
      
      .list-wrapper
        position: absolute
        top: 165px
        bottom: 0
        width: 100%
        .list-scroll
          height: 100%
          overflow: hidden
          .list-inner
            padding: 20px 30px
    .search-result
      position: fixed
      top: 124px
      bottom: 0
      width: 100%
      
    .tip-title
      text-align: center
      padding: 18px 0
      font-size: 0
      .icon-ok
        font-size: $font-size-medium
        color: $color-theme
        margin-right: 4px
      .text
        font-size: $font-size-medium
        color: $color-text
</style>