<template>
  <transition name="list-fade">
    <div class="playlist" v-show="showFlag" @click="hide">
      <div class="list-wrapper" @click.stop>
        <div class="list-header">
          <h1 class="title">
            <i class="icon" :class="iconMode" @click="changeMode"></i>
            <span class="text">{{modeText}}</span>
            <span class="clear" @click="showConfirm"><i class="icon-clear"></i></span>
          </h1>
        </div>
        <scroll :data="sequenceList" 
        :pull-up-load="true" class="list-content" 
        ref="scroll" :refreshDelay="refreshDelay">
          <transition-group tag="ul" name="list">
            <li class="item" v-for="(item,index) in sequenceList" 
            :key="item.id" @click="selectItem(item,index)" ref="listItem">
              <i class="current" :class="getCurrentIcon(item)"></i>
              <span class="text">{{item.name}}</span>
              <span class="like" @click.stop="toggleFavorite(item)">
                <i :class="getFavorite(item)"></i>
              </span>
              <span class="delete" @click.stop="deleteOne(item)">
                <i class="icon-delete"></i>
              </span>
            </li>
          </transition-group>
        </scroll>
        <div class="list-operate">
          <div class="add">
            <i class="icon-add"></i>
            <span class="text" @click="addSong">添加歌曲到队列</span>
          </div>
        </div>
        <div class="list-close" @click="hide">
          <span>关闭</span>
        </div>
      </div>
      <confirm title='是否清空列表' ref="confirm" @clear="clearList" />
      <add-song ref="add"></add-song>
    </div>
  </transition>
</template>

<script>

import {mapActions} from 'vuex'
import {playMode} from 'common/config.js'
import {playingMixin} from 'common/mixin.js'
import Scroll from 'common/scroll/Scroll.vue'
import Confirm from 'common/confirm/Confirm.vue'
import AddSong from 'content/addsong/AddSong.vue'
  export default {
    mixins: [playingMixin],
    data() {
      return {
        showFlag:false,
        refreshDelay:100
      }
    },
    computed: {
      modeText(){
        return this.mode === playMode.sequence ? '顺序播放' : 
        this.mode === playMode.random ? '随机播放' : '循环播放'
      }
    },
    methods: {
      show(){
        this.showFlag = true
        setTimeout(() => {//打开列表时刷新
          this.$refs.scroll.refresh()
           this.scrollToCurrent(this.currentSong)//currentSong在mixin里
        },20)
       
      },
      hide(){
        this.showFlag = false
      },
      getCurrentIcon(item){
        if( this.currentSong.id === item.id ){
          return 'icon-play'
        }else{
          return ''
        }
      },
      selectItem(item,index){//播放选择的歌曲
        if (this.mode === playMode.random ){
          index = this.playList.findIndex((song) => {
          return  song.id === item.id
          })
        }
        this.SET_CURRENT_INDEX(index)
        this.SET_PLAYING(true)
      },
      scrollToCurrent(current){
        const index = this.sequenceList.findIndex((song) => {
          return current.id === song.id//当id相等时
        })
        this.$refs.scroll.scrollToElement(this.$refs.listItem[index],300)
      },
      deleteOne(item){
        this.deleteSong(item)
        if(!this.playList.length){
          this.hide()
        }
      },
      showConfirm(){
        this.$refs.confirm.show()
      },
      clearList(){
        this.deleteList()
      
      },
      addSong(){
        this.$refs.add.show()
      },
      ...mapActions(['deleteSong','deleteList'])
    },
    components: {
      Scroll,
      Confirm,
      AddSong
    },
    watch: {
      currentSong(newSong,oldSong){
        if(!this.showFlag || newSong.id === oldSong.id){
          return
        }
        this.scrollToCurrent(newSong)
      }
    },
  }
</script>

<style scoped lang="stylus">
  @import "~assets/stylus/variable"
  @import "~assets/stylus/mixin"

  .playlist
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 200
    background-color: $color-background-d
    &.list-fade-enter-active, &.list-fade-leave-active
      transition: opacity 0.3s
      .list-wrapper
        transition: all 0.3s
    &.list-fade-enter, &.list-fade-leave-to
      opacity: 0
      .list-wrapper
        transform: translate3d(0, 100%, 0)
    &.list-fade-enter
    .list-wrapper
      position: absolute
      left: 0
      bottom: 0
      width: 100%
      background-color: $color-highlight-background
      .list-header
        position: relative
        padding: 20px 30px 10px 20px
        .title
          display: flex
          align-items: center
          .icon
            margin-right: 10px
            font-size: 30px
            color: $color-theme-d
          .text
            flex: 1
            font-size: $font-size-medium
            color: $color-text-l
          .clear
            extend-click()
            .icon-clear
              font-size: $font-size-medium
              color: $color-text-d
      .list-content
        max-height: 240px
        overflow: hidden
        .item
          display: flex
          align-items: center
          height: 40px
          padding: 0 30px 0 20px
          overflow: hidden
          &.list-enter-active, &.list-leave-active
            transition: all 0.1s
          &.list-enter, &.list-leave-to
            height: 0
          .current
            flex: 0 0 20px
            width: 20px
            font-size: $font-size-small
            color: $color-theme-d
          .text
            flex: 1
            no-wrap()
            font-size: $font-size-medium
            color: $color-text-d
          .like
            extend-click()
            margin-right: 15px
            font-size: $font-size-small
            color: $color-theme
            .icon-favorite
              color: $color-sub-theme
          .delete
            extend-click()
            font-size: $font-size-small
            color: $color-theme
      .list-operate
        width: 140px
        margin: 20px auto 30px auto
        .add
          display: flex
          align-items: center
          padding: 8px 16px
          border: 1px solid $color-text-l
          border-radius: 100px
          color: $color-text-l
          .icon-add
            margin-right: 5px
            font-size: $font-size-small-s
          .text
            font-size: $font-size-small
      .list-close
        text-align: center
        line-height: 50px
        background: $color-background
        font-size: $font-size-medium-x
        color: $color-text-l
</style>