<template>
<div class="song-list">
  <ul>
    <li v-for="(song,index) in songs" class="item"
    :key="index" @click="selectItem(song,index)">
      <div class="rank" v-show="rank">
        <span :class="getRankCls(index)">{{getRankText(index)}}</span>
      </div>
      <div class="content">
        <h2 class="name">{{song.name}}</h2>
        <p class="desc">{{getDesc(song)}}</p>
      </div>
    </li>
  </ul>
</div>
</template>

<script>
const EVENT_SELECT='selectItem'
export default {
  name:'SongList',
  props:{
    songs:{
      type:Array,
      default(){
        return []
      }
    },
    rank:{
      type:Boolean,
      dafault:false
    }
  },
components: {},
data() {
return {

}
},
methods: {
  getDesc(song){
    return song.album.name ? `${song.singer[0].name} • ${song.album.name}` : `${song.singer[0].name}`
  },
  selectItem(item,index){
    this.$emit(EVENT_SELECT,item,index)
  },
  getRankCls(index){
    if(index<=2){
      return `icon icon${index}`
    }else{
      return 'text'
    }
  },
  getRankText(index){
    if(index>2){
      return index+1
    }
  },
}
}
</script>
<style lang='stylus' scoped>
@import "~assets/stylus/variable"
@import "~assets/stylus/mixin"

  .song-list
    .item
      display: flex
      align-items: center
      box-sizing: border-box
      height: 64px
      font-size: $font-size-medium
      .rank
        flex: 0 0 25px
        width: 25px
        margin-right: 30px
        text-align: center
        .icon
          display: inline-block
          width: 25px
          height: 24px
          background-size: 25px 24px
          &.icon0
            bg-image('first')
          &.icon1
            bg-image('second')
          &.icon2
            bg-image('third')
        .text
          color: $color-theme
          font-size: $font-size-large
    .content
      flex: 1
      line-height: 20px
      overflow: hidden
      .name
        no-wrap()
        color: $color-text
      .desc
        no-wrap()
        margin-top: 4px
        color: $color-text-d
</style>